import { DATE_FORMAT_STRING } from "@/config";
import { emitter } from "@/event-bus";
import format from "date-fns/format";
import { useToast } from "primevue/usetoast";
import { computed, inject, reactive, ref } from "vue";
import { useStore } from "vuex";

export function useComplicationFormInit() {
    const toast = useToast();
    const store = useStore();
    const form = reactive({
        complications: null,
        spermTooLowCount: null,
        treatmentOutcome: null,
        typeOfPregnancy: null,
        fetalSacsUS: null,
        fetalheartsUS: null,
        chorionicity: null,
        electiveFetalReduction: null,
        pregnancyBirthOutcome: null,
        newbornDateBirth: null,
        pregnancyLossDate: null,
        babiesborn: null,
        gestationalWeeks: null,
        gestationalDays: null
    });
    const loading = ref(false);
    const complications = computed(() => store.state.born.complications);

    const fillForm = ({
        complications,
        spermTooLowCount,
        treatmentOutcome,
        typeOfPregnancy,
        fetalSacsUS,
        fetalheartsUS,
        chorionicity,
        electiveFetalReduction,
        pregnancyBirthOutcome,
        newbornDateBirth,
        pregnancyLossDate,
        babiesborn,
        gestationalWeeks,
        gestationalDays
    }) => {
        form.complications = complications;
        form.spermTooLowCount = spermTooLowCount;
        form.treatmentOutcome = treatmentOutcome;
        form.typeOfPregnancy = typeOfPregnancy;
        form.fetalSacsUS = fetalSacsUS;
        form.fetalheartsUS = fetalheartsUS;
        form.chorionicity = chorionicity;
        form.electiveFetalReduction = electiveFetalReduction;
        form.pregnancyBirthOutcome = pregnancyBirthOutcome;
        form.newbornDateBirth = newbornDateBirth;
        form.pregnancyLossDate = pregnancyLossDate;
        form.babiesborn = babiesborn;
        form.gestationalWeeks = gestationalWeeks;
        form.gestationalDays = gestationalDays;
    };

    const save = () => {
        loading.value = true;
        const data = {
            ...form,
            newbornDateBirth: format(
                new Date(form.newbornDateBirth),
                DATE_FORMAT_STRING
            ),
            pregnancyLossDate: format(
                new Date(form.pregnancyLossDate),
                DATE_FORMAT_STRING
            ),
            ...store.getters["artCycle/getSelectedCycleAndPatientId"]
        };

        store
            .dispatch("born/updateComplication", data)
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "",
                    detail: "Save Successful",
                    life: 3000
                });
            })
            .catch((err) => {})
            .finally(() => (loading.value = false));
    };

    const complicationOptions = store.getters["utils/getComplicationsOptions"];
    const treatmentOutcomeOptions =
        store.getters["utils/getTreatmentOutcomeOptions"];
    const typeOfPregnancyOptions =
        store.getters["utils/getTypeOfPregnancyOptions"];
    const chorionicityOptions = store.getters["utils/getChorionicityOptions"];
    const electiveFetalReductionOptions =
        store.getters["utils/getElectiveFetalReductionOptions"];
    const pregnancyBirthOutcomeOptions =
        store.getters["utils/getPregnancyBirthOutcomeOptions"];

    return {
        form,
        save,
        complications,
        fillForm,
        loading,
        complicationOptions,
        treatmentOutcomeOptions,
        typeOfPregnancyOptions,
        chorionicityOptions,
        electiveFetalReductionOptions,
        pregnancyBirthOutcomeOptions
    };
}

