export function useBornEmbryologyInit() {
    const store = useStore();
    const form = inject("form");

    const isIVFCycle = store.getters["artCycle/isIVFCycle"];
    const isFETCycle = store.getters["artCycle/isFETCycle"];

    const inseminationMethodOptions =
        store.getters["utils/getInseminationMethodOptions"];
    const reasonsForNoUseOptions =
        store.getters["utils/getReasonsForNoUseOptions"];
    const embryoStageFreezingOptions =
        store.getters["utils/getEmbryoStageFreezingOptions"];
    const reasonEmbryoCryopreservationOptions =
        store.getters["utils/getReasonEmbryoCryopreservationOptions"];
    const embryoFreshCryoMethodOptions =
        store.getters["utils/getEmbryoFreshCryoMethodOptions"];
    const embryoStageThawingOptions =
        store.getters["utils/getEmbryoStageThawingOptions"];
    const embryoTransferOptions = store.getters["utils/getReasonsNoEtOptions"];

    return {
        form,
        isIVFCycle,
        isFETCycle,
        inseminationMethodOptions,
        reasonsForNoUseOptions,
        embryoStageFreezingOptions,
        reasonEmbryoCryopreservationOptions,
        embryoFreshCryoMethodOptions,
        embryoStageThawingOptions,
        embryoTransferOptions
    };
}

